import { useSearchParams } from 'react-router-dom';

import { useRequest } from '@miq/hookjs';
import { StaffProduct } from './models';

export const useProductListRequest = (
  orderSlug?: string,
  filter?: { published?: 'include' | 'exclude'; atc?: 0 | 1 }
) => {
  // TODO: order slug is not used yet
  const [params] = useSearchParams();

  const __no_des = params.get('__no_des');
  const __no_size = params.get('__no_size');
  const __oos = params.get('__oos');
  const q = params.get('q');
  const cat = params.get('cat');
  const page = params.get('page');
  const published = params.get('published');
  const presale = params.get('presale');
  const sale = params.get('sale');

  let searchParams: any = { ...params, ...filter };
  if (orderSlug) searchParams.supplier_order_slug = orderSlug;

  return useRequest(() => StaffProduct({}).list({ params }), {
    refreshDeps: [q, page, cat, published, presale, sale, orderSlug, __no_des, __no_size, __oos],
  });
};
